export default "<template>\r\n  <div class=\"tpl\">\r\n    <raw-loader :mytxt=\" require('!raw-loader!./TabulaFirst.vue').default \"></raw-loader>\r\n    <h1>This is Tabulator First [ {{$route.params.id}} ]</h1>\r\n    <div>\r\n      <div class=\"mb-1\">\r\n        <button class=\"mr-1\" ref=\"test2\" >replaceData</button>\r\n      </div>\r\n      <div id=\"example-table\"></div>\r\n      <div><pre>{{msg}}</pre></div>\r\n    </div>\r\n  </div>\r\n</template>\r\n<script>\r\nimport {TabulatorFull as Tabulator} from 'tabulator-tables';\r\nimport $ from \"jquery\";\r\nexport default {\r\n  name: 'Tpl',\r\n  components: {\r\n  },\r\n  data(){\r\n    return {\r\n      msg:\"thuc 101\"\r\n    }\r\n  },\r\n  mounted(){\r\n    let that=this;\r\n    var tabledata = [\r\n      {id:1, name:\"Oli Bob\", progress:12, gender:\"male\", rating:1, col:\"red\", dob:\"19/02/1984\", car:1},\r\n      {id:2, name:\"Mary May\", progress:1, gender:\"female\", rating:2, col:\"blue\", dob:\"14/05/1982\", car:true},\r\n      {id:3, name:\"Christine Lobowski\", progress:42, gender:\"female\", rating:0, col:\"green\", dob:\"22/05/1982\", car:\"true\"},\r\n      {id:4, name:\"Brendon Philips\", progress:100, gender:\"male\", rating:1, col:\"orange\", dob:\"01/08/1980\"},\r\n      {id:5, name:\"Margret Marmajuke\", progress:16, gender:\"female\", rating:5, col:\"yellow\", dob:\"31/01/1999\"},\r\n      {id:6, name:\"Frank Harbours\", progress:38, gender:\"male\", rating:4, col:\"red\", dob:\"12/05/1966\", car:1},\r\n    ];\r\n    var table = new Tabulator(\"#example-table\", {\r\n      data:tabledata,           //load row data from array\r\n      layout:\"fitColumns\",      //fit columns to width of table\r\n      responsiveLayout:\"hide\",  //hide columns that dont fit on the table\r\n      tooltips:true,            //show tool tips on cells\r\n      addRowPos:\"top\",          //when adding a new row, add it to the top of the table\r\n      history:true,             //allow undo and redo actions on the table\r\n      pagination:\"local\",       //paginate the data\r\n      paginationSize:3,         //allow 7 rows per page of data\r\n      paginationCounter:\"rows\", //display count of paginated rows in footer\r\n      movableColumns:true,      //allow column order to be changed\r\n      initialSort:[             //set the initial sort order of the data\r\n          {column:\"name\", dir:\"asc\"},\r\n      ],\r\n      height:'300px',\r\n      columns:[                 //define the table columns\r\n          {title:\"Name\", field:\"name\", editor:\"input\"},\r\n          {title:\"Task Progress\", field:\"progress\", hozAlign:\"left\", formatter:\"progress\", editor:true},\r\n          {title:\"Gender\", field:\"gender\", width:95, editor:\"select\", editorParams:{values:[\"male\", \"female\"]}},\r\n          {title:\"Rating\", field:\"rating\", formatter:\"star\", hozAlign:\"center\", width:100, editor:true},\r\n          {title:\"Color\", field:\"col\", width:130, editor:\"input\"},\r\n          {title:\"Date Of Birth\", field:\"dob\", width:130, sorter:\"date\", hozAlign:\"center\"},\r\n          {title:\"Driver\", field:\"car\", width:90,  hozAlign:\"center\", formatter:\"tickCross\", sorter:\"boolean\", editor:true},\r\n      ],\r\n    });\r\n    $(this.$refs.test2).on(\"click\",function(){\r\n      var data = table.getData();\r\n      let ar=[];\r\n      ar.push(data[0]);\r\n      table.replaceData(ar);\r\n    })\r\n  },\r\n  methods:{\r\n  }\r\n}\r\n</script>\r\n<style scoped>\r\n  @import '~tabulator-tables/dist/css/tabulator.css';  \r\n  /*@import \"tabulator-tables\"*/\r\n\r\n.tabulator {\r\n   font-size: 10px;\r\n   border: none;\r\n}\r\n\r\n.tabulator .tabulator-header {\r\n   font-weight: normal;\r\n   border-bottom: 1px solid;\r\n   border-bottom-color: rgba(0, 0, 0, 0.12);\r\n   color: rgba(0, 0, 0, 0.54);\r\n}\r\n\r\n.tabulator .tabulator-header .tabulator-col {\r\n   height: 48px;\r\n   background-color: #ffffff;\r\n   border: none;\r\n   padding: 0 7px;\r\n}\r\n\r\n.tabulator-row {\r\n   border-bottom: 1px solid;\r\n   border-bottom-color: rgba(0, 0, 0, 0.12);\r\n   min-height: 48px;\r\n   vertical-align: middle;\r\n}\r\n\r\n.tabulator-row .tabulator-cell {\r\n   padding: 16px 7px;\r\n   border-right: none;\r\n   vertical-align: middle;\r\n}\r\n\r\n.tabulator-row .tabulator-cell:first-of-type,\r\n.tabulator .tabulator-header .tabulator-col:first-of-type {\r\n   padding-left: 24px;\r\n}\r\n\r\n.tabulator-row.tabulator-row-even {\r\n   background-color: #ffffff;\r\n}\r\n\r\n\r\n\r\n</style>\r\n\r\n";